import {List} from 'immutable';
import * as React from 'react';
import {FormEvent, useEffect, useState} from 'react';
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Button, ButtonGroup} from "semantic-ui-react";
import {useElementsTree} from "../../../hooks/useElementsTree";
import {useGraph} from "../../../hooks/useGraph";
import {ElementsTree} from "../../../model/ElementsTree";
import {
  convertNodeToJs,
  Graph,
  makeNodeFromJs,
  NextToPosition,
  Node,
  NodeMetadata,
  NodeType
} from "../../../model/Graph";
import {
  ImmutableInformationContext,
  informationContextFromNode
} from "../../../service/cody-wizard/context/information-context";
import {updateContext} from "../../../service/cody-wizard/context/wizard-context";
import {getAggregateEventMetadata} from "../../../service/cody-wizard/event/get-aggregate-event-metadata";
import {markAsConnected} from '../../../service/cody-wizard/node/mark-as-connected';
import {Rule} from '../../../service/cody-wizard/rule-engine/configuration';
import {
  convertDependenciesToMetadataConfig
} from '../../../service/cody-wizard/rule-engine/convert-dependencies-to-metadata-config';
import {getDependencyName} from '../../../service/cody-wizard/rule-engine/get-dependency-name';
import {suggestRulesDependencyConfig} from '../../../service/cody-wizard/rule-engine/suggest-rules-dependency-config';
import {RulesDependency} from '../../../service/cody-wizard/rule-engine/types';
import {Schema} from "../../../service/cody-wizard/schema/schema";
import {isPropertyRef, splitPropertyRef} from "../../../service/cody-wizard/schema/shorthand";
import {TableColumn, UiSchema} from "../../../service/cody-wizard/ui/ui-schema";
import {getConnectedEventIfPossible} from "../../../service/cody-wizard/vo/get-connected-event-if-possible";
import {
  getConnectedValueObjectSourceIfPossible
} from "../../../service/cody-wizard/vo/get-connected-value-object-source-if-possible";
import {getSchemaRefName} from "../../../service/cody-wizard/vo/get-schema-ref-name";
import {getVoMetadata, ResolveConfig, ValueObjectMetadata} from "../../../service/cody-wizard/vo/get-vo-metadata";
import {isListVo} from "../../../service/cody-wizard/vo/is-list-vo";
import {isVoStoredInDb} from '../../../service/cody-wizard/vo/is-vo-stored-in-db';
import DependenciesEditor from '../Editor/DependenciesEditor';
import EditorDivider from "../Editor/EditorDivider";
import InitializeRulesEditor from '../Editor/InitializeRulesEditor';
import QuerySchemaEditor from "../Editor/QuerySchemaEditor";
import ResolveEditor from "../Editor/ResolveEditor";
import SchemaEditor from "../Editor/SchemaEditor";
import UiSchemaEditor from "../Editor/UiSchemaEditor";
import ChangeNodeNameOrDeleteNode from "../Node/ChangeNodeNameOrDeleteNode";
import MultipleNodeSelect from '../Node/MultipleNodeSelect';
import {DEFAULT_CONNECT_MARGIN, WithWizardStepProps} from "../WizardModal";
import AdvancedButton from './AdvancedButton';
import StoredVoToggle from './AnyVO/StoredVoToggle';
import NamespaceInput from "./StateVO/NamespaceInput";

interface OwnProps {

}

type AnyVoProps = OwnProps & WithWizardStepProps<ImmutableInformationContext> & WithNamespaces;

const AnyVo = (props: AnyVoProps) => {
  const [graph] = useGraph();
  const elementsTree = useElementsTree(props.ctx.board.uid);
  const [metadata, setMetadata] = useState<ValueObjectMetadata>({});
  const [autoFocusSchema, setAutoFocusSchema] = useState(false);
  const [nodeName, setNodeName] = useState(props.ctx.vo? props.ctx.vo.getName() : '');
  const [voSchema, setVoSchema] = useState(new Schema({}));
  const [uiSchema, setUiSchema] = useState({});
  const [querySchema, setQuerySchema] = useState(new Schema({}));
  const [showAdvancedSettings, setShowAdvancedSettings] = useState(false);
  const [dependencies, setDependencies] = useState<Record<string, RulesDependency>>({});
  const [showDependencies, setShowDependencies] = useState(false);

  const updateMetadata = (meta: ValueObjectMetadata) => {
    if(props.onUpdateMetadata) {
      props.onUpdateMetadata(JSON.stringify(meta, null, 2), true);
    }

    if(meta.schema && JSON.stringify(meta.schema) !== JSON.stringify(voSchema)) {
      setVoSchema(meta.schema);
    }

    if(meta.uiSchema && JSON.stringify(meta.uiSchema) !== JSON.stringify(uiSchema)) {
      setUiSchema(meta.uiSchema);
    }

    if(meta.querySchema && JSON.stringify(meta.querySchema) !== JSON.stringify(querySchema)) {
      setQuerySchema(meta.querySchema);
    }

    setMetadata(meta);
  }

  const updateDependencies = (deps: Record<string, RulesDependency>) => {
    if(props.mode === "sidebar" && props.ctx.vo) {
      const shouldRepositionFeature = updateDependenciesOnGraph(props.ctx.vo, props.ctx, List(Object.values(deps).map(dep => dep.node)), graph);
      // Refresh context
      const changedCtx = informationContextFromNode(graph.getNode(props.ctx.vo.getId())!, props.ctx.board);
      props.onCtxChanged(changedCtx);

    }

    setDependencies(deps);
  }

  useEffect(() => {
    resetForm();

    if(props.ctx.vo) {
      const stateMeta = getVoMetadata(props.ctx.vo);
      let shouldUpdateMetadata = false;

      if(stateMeta.schema && !stateMeta.schema.isEmpty()) {
        setVoSchema(stateMeta.schema);
      } else {
        stateMeta.schema = suggestVoSchema(props.ctx.vo);
        shouldUpdateMetadata = true;
      }

      if(Object.keys(props.ctx.queryDependencies).length) {
        setShowDependencies(true);

        if(!stateMeta.queryDependencies || stateMeta.queryDependencies !== convertDependenciesToMetadataConfig(props.ctx.queryDependencies)) {
          stateMeta.queryDependencies = convertDependenciesToMetadataConfig(props.ctx.queryDependencies);
          shouldUpdateMetadata = true;
        }
      }

      setDependencies(props.ctx.queryDependencies);

      if(stateMeta.uiSchema) {
        const suggestedUiSchema = suggestUiSchema(stateMeta.schema || new Schema({}), stateMeta.uiSchema, elementsTree);

        if(JSON.stringify(stateMeta.uiSchema) !== JSON.stringify(suggestedUiSchema)) {
          shouldUpdateMetadata = true;
        }

        setUiSchema(suggestedUiSchema || {});
        stateMeta.uiSchema = suggestedUiSchema;
      } else {
        const suggestedUiSchema = suggestUiSchema(stateMeta.schema || new Schema({}), stateMeta.uiSchema, elementsTree);

        if(suggestedUiSchema) {
          shouldUpdateMetadata = true;
          setUiSchema(suggestedUiSchema);
          stateMeta.uiSchema = suggestedUiSchema;
        }
      }

      if(stateMeta.querySchema) {
        setQuerySchema(stateMeta.querySchema);
      }

      if(!stateMeta.ns || stateMeta.ns === "/") {
        stateMeta.ns = suggestVoNamespace(props.ctx.vo);
      }

      setNodeName(props.ctx.vo.getName());

      if(shouldUpdateMetadata) {
        const sourceVo = getConnectedValueObjectSourceIfPossible(props.ctx.vo);

        if(sourceVo) {
          if(!stateMeta.querySchema) {
            stateMeta.querySchema = new Schema({});
          }

          if(!stateMeta.resolve) {
            stateMeta.resolve = getDefaultResolveConfigFromQuery(stateMeta.querySchema);
          }
        }

        updateMetadata(stateMeta);
        return;
      }

      setMetadata(stateMeta);
    }

  }, [props.ctx.vo])

  const resetForm = () => {
    setMetadata({});
    setAutoFocusSchema(false);
    setNodeName('');
    setVoSchema(new Schema({}));
    setUiSchema({});
    setQuerySchema(new Schema({}));
    setDependencies({});
    setShowDependencies(false);
  }

  const handleSubmit = (evt: FormEvent) => {
    evt.preventDefault();
    evt.stopPropagation();
  }

  const handleNameChanged = (newNodeName: string) => {
    if(props.ctx.vo) {
      graph.changeNodeName(props.ctx.vo, newNodeName);
      props.onCtxChanged(updateContext(props.ctx, {vo: props.ctx.vo.changeName(newNodeName)}))
    }
  }

  const handleNsChanged = (newNs: string) => {
    updateMetadata({...metadata, ns: newNs});
  }

  const handleSchemaChanged = (newSchema: Schema) => {
    const suggestedUiSchema = suggestUiSchema(newSchema, uiSchema, elementsTree);

    if(JSON.stringify(uiSchema) !== JSON.stringify(suggestedUiSchema)) {
      setUiSchema(suggestedUiSchema || {});
    }

    updateMetadata({...metadata, schema: newSchema, uiSchema: suggestedUiSchema});
  }

  const handleQuerySchemaChanged = (newQuerySchema: Schema) => {
    const resolve = metadata.resolve || getDefaultResolveConfigFromQuery(newQuerySchema);
    updateMetadata({...metadata, querySchema: newQuerySchema, resolve});
  }

  const handleQuerySchemaRemoved = () => {
    updateMetadata({...metadata, querySchema: undefined, resolve: undefined});
  }

  const handleDependenciesChanged = (changedDependencies: List<Node>) => {
    const newDependencies: Record<string, RulesDependency> = {};

    changedDependencies.forEach(dep => {
      const depName = getDependencyName(dep, props.ctx);
      if(!dependencies[depName]) {
        newDependencies[depName] = suggestRulesDependencyConfig(dep, props.ctx);
      } else {
        newDependencies[depName] = dependencies[depName];
      }
    })

    updateDependencies(newDependencies);
    updateMetadata({...metadata, queryDependencies: convertDependenciesToMetadataConfig(newDependencies)});
  }

  const handleDependenciesConfigChanged = (changedDependencies: Record<string, RulesDependency>) => {
    updateMetadata({...metadata, queryDependencies: convertDependenciesToMetadataConfig(changedDependencies)});
    window.setTimeout(() => {
      updateDependencies(changedDependencies);
    },100);
  }

  const handleResolveChanged = (resolve?: ResolveConfig) => {
    updateMetadata({...metadata, resolve});
  }

  const handleUiSchemaChanged = (newUiSchema: UiSchema) => {
    updateMetadata({...metadata, uiSchema: newUiSchema});
  }

  const handleStoredInDbChanged = (isStoredInDb: boolean) => {
    // @TODO: suggest collection
    if(isStoredInDb) {
      const newMeta = {...metadata};
      delete newMeta.collection;
      updateMetadata(newMeta);
    } else {
      updateMetadata({...metadata, collection: false});
    }
  }

  const handleInitializeRulesChanged = (rules: Rule[]) => {
    updateMetadata({...metadata, initialize: rules});
  }

  props.onFinish((): ImmutableInformationContext => {
    if(!props.ctx.vo) {
      resetForm();
      return props.ctx;
    }

    const changedVo = makeNodeFromJs({...convertNodeToJs(props.ctx.vo), ...{name: nodeName, metadata: JSON.stringify(metadata, null, 2)}})
    graph.changeNodeMetadata(changedVo, changedVo.getMetadata() as NodeMetadata);

    resetForm();
    return {...props.ctx, vo: changedVo};
  })

  const depsOptions = elementsTree.getFilteredElementsByTypes([NodeType.document, NodeType.externalSystem]);

  return <form className='ui form' onSubmit={handleSubmit}>
    {props.mode === 'modal' && <ChangeNodeNameOrDeleteNode node={props.ctx.vo} board={props.ctx.board} onNameChanged={handleNameChanged} disableDelete={true} />}
    <EditorDivider content="Namespace" />
    <NamespaceInput ns={metadata.ns || ''} onNsChanged={handleNsChanged} disabled={!props.ctx.vo.isEnabled()} />
    <EditorDivider content="Schema" />
    <SchemaEditor boardId={props.ctx.board.uid}
                  sessionId={props.ctx.vo? props.ctx.vo.getId() : undefined }
                  schema={voSchema}
                  readonly={!props.ctx.vo.isEnabled()}
                  onSchemaChanged={handleSchemaChanged} autofocus={autoFocusSchema}
    />
    <EditorDivider content="Query Schema" />
    <QuerySchemaEditor boardId={props.ctx.board.uid}
                       sessionId={props.ctx.vo? props.ctx.vo.getId() : undefined }
                       schema={querySchema}
                       readonly={!props.ctx.vo.isEnabled()}
                       onSchemaChanged={handleQuerySchemaChanged}
                       onSchemaRemoved={handleQuerySchemaRemoved}
    />
    <div className={'field'}>
      {<Button basic={true} icon={showDependencies? 'minus' : 'add'} labelPosition="left" size="mini"
               content={props.t('insp.cody_wizard.step_policy.manage_dependencies') + ` (${Object.keys(dependencies).length})`} color="blue" className="text"
               onClick={() => setShowDependencies(!showDependencies)}
               disabled={!props.ctx.vo}/>}
    </div>
    <div className={(showDependencies? 'slidedown' : 'slideup') + ' smooth'} >
      <EditorDivider content={props.t('insp.cody_wizard.step_policy.dependencies_label') as string} />
      <MultipleNodeSelect selected={List(Object.values(dependencies).map(dep => dep.node))}
                          nodeOptions={depsOptions}
                          placeholder={props.t('insp.cody_wizard.step_policy.dependencies_placeholder') as string}
                          withNs={true}
                          uniqueName={true}
                          onChange={handleDependenciesChanged}
                          disabled={!props.ctx.vo || !props.ctx.vo.isEnabled()} />
      <DependenciesEditor boardId={props.ctx.board.uid}
                          sessionId={props.ctx.vo ? props.ctx.vo.getId() : undefined }
                          dependencies={dependencies}
                          messageSchema={querySchema}
                          autofocus={false}
                          readonly={!!(props.ctx.vo && !props.ctx.vo.isEnabled())}
                          onDependenciesChanged={handleDependenciesConfigChanged}
      />

    </div>
    <EditorDivider content="Query Resolver" />
    <ResolveEditor boardId={props.ctx.board.uid}
                   sessionId={props.ctx.vo? props.ctx.vo.getId() : undefined }
                   nodeSchema={voSchema}
                   querySchema={querySchema}
                   resolve={metadata.resolve}
                   readonly={!props.ctx.vo.isEnabled()}
                   onResolveChanged={handleResolveChanged}
    />
    <EditorDivider content="UI Schema" />
    <UiSchemaEditor boardId={props.ctx.board.uid}
                    sessionId={props.ctx.vo? props.ctx.vo.getId() : undefined }
                    nodeSchema={voSchema}
                    schema={uiSchema}
                    readonly={!props.ctx.vo.isEnabled()}
                    onSchemaChanged={handleUiSchemaChanged}
                    tableSchema={isListVo(props.ctx.vo)}
    />
    <ButtonGroup>
      <Button basic={true} compact={true} icon="search" className="noborder" content={props.t('insp.cody_wizard.ui_schema_editor.choose_icon')} as="a" href="https://pictogrammers.com/library/mdi/" target="_blank" />
    </ButtonGroup>
    <AdvancedButton showAdvancedSettings={showAdvancedSettings} onShowChanged={show => setShowAdvancedSettings(show)}  disabled={!props.ctx.vo} />
    <div className={(showAdvancedSettings? 'slidedown' : 'slideup') + ' smooth'}>
      <StoredVoToggle isStored={isVoStoredInDb(metadata)} ctx={props.ctx} vo={props.ctx.vo} onStoredStateChanged={handleStoredInDbChanged} />
      <EditorDivider content="Initialize Rules" />
      <InitializeRulesEditor elementId={props.ctx.vo ? props.ctx.vo.getId() : ''}
                             sessionId={props.ctx.vo ? props.ctx.vo.getId() : undefined }
                             initializeRules={metadata.initialize || []}
                             dataSchema={voSchema}
                             readonly={!!(props.ctx.vo && !props.ctx.vo.isEnabled())}
                             ctx={props.ctx}
                             onRulesChanged={handleInitializeRulesChanged}
      />
    </div>
    <div style={{marginTop: '100px'}} />
  </form>
};

export default withNamespaces()(AnyVo);

const suggestVoSchema = (vo: Node): Schema => {
  const sourceVo = getConnectedValueObjectSourceIfPossible(vo);

  if(!sourceVo) {
    const event = getConnectedEventIfPossible(vo);

    if(event) {
      const evtMeta = getAggregateEventMetadata(event);

      if(evtMeta.schema) {
        return evtMeta.schema;
      }
    }

    return new Schema({});
  }

  const sourceMeta = getVoMetadata(sourceVo);

  const sourceRefName = getSchemaRefName(sourceVo, sourceMeta);

  return new Schema({$items: sourceRefName});
}

export const suggestUiSchema = (schema: Schema, uiSchema: UiSchema | undefined, elementsTree: ElementsTree): UiSchema | undefined => {
  const suggestions: Record<string, any> = {};

  if(schema.isObject()) {
    schema.getObjectProperties().forEach(prop => {
      const propSchema = schema.getObjectPropertySchema(prop, new Schema({}));

      if(propSchema.isString('uuid')) {
        suggestions[prop] = {
          "ui:widget": "hidden"
        }
      } else if(propSchema.isRef() && isPropertyRef(propSchema.getRef(''))) {
        const [ref, refProp] = splitPropertyRef(propSchema.getRef(''));

        const refVoSchema = propSchema.resolveRef(elementsTree);
        const refPropSchema = refVoSchema.getObjectPropertySchema(refProp, new Schema({}));
        let firstStringProp = refProp;

        refVoSchema.getObjectProperties().forEach(oProp => {
          const oPropSchema = refVoSchema.getObjectPropertySchema(oProp, new Schema({}));

          if(oPropSchema.isString() && !oPropSchema.isString('uuid')) {
            firstStringProp = oProp;
          }
        })

        if(refPropSchema.isString('uuid')) {
          let firstVoList: Node | undefined;
          let firstVoListMeta: ValueObjectMetadata | undefined;
          elementsTree.getFilteredElementsByType(NodeType.document).forEach(doc => {
            if(isListVo(doc)) {
              const listVoMeta = getVoMetadata(doc);
              const listVoSchema = listVoMeta.schema || new Schema({});

              if(listVoSchema.getListItemsSchema(new Schema({})).getRef() === ref) {
                firstVoList = doc;
                firstVoListMeta = listVoMeta;
              }
            }
          })

          if(firstVoList) {
            suggestions[prop] = {
              "ui:widget": "DataSelect",
              "ui:options": {
                "data": getSchemaRefName(firstVoList, firstVoListMeta!),
                "value": `data.${refProp}`,
                "text": `data.${firstStringProp}`
              }
            }
          }
        }
      }
    })

    if(!Object.keys(suggestions).length) {
      return uiSchema;
    }

    return {...suggestions, ...uiSchema};
  }

  if(schema.isList() && schema.getListItemsSchema(new Schema({})).isRef()) {
    const listItemSchema = schema.getListItemsSchema(new Schema({})).resolveRef(elementsTree);

    const columns: TableColumn[] = [];

    if(!listItemSchema.isObject()) {
      return uiSchema;
    }

    listItemSchema.getObjectProperties().forEach(prop => {
      const propSchema = listItemSchema.getObjectPropertySchema(prop, new Schema({}));

      if(propSchema.isPrimitive() && !propSchema.isString('uuid')) {
        columns.push(prop);
      }

      if(propSchema.isRef()) {
        const ref = propSchema.getRef('');
        const [refWithoutProp, refProp] = splitPropertyRef(ref);

        if(refProp) {
          const refVoSchema = propSchema.resolveRef(elementsTree);
          const refPropSchema = refVoSchema.getObjectPropertySchema(refProp, new Schema({}));
          let firstStringProp = refProp;


          refVoSchema.getObjectProperties().forEach(oProp => {
            const oPropSchema = refVoSchema.getObjectPropertySchema(oProp, new Schema({}));

            if(oPropSchema.isString() && !oPropSchema.isString('uuid')) {
              firstStringProp = oProp;
            }
          })

          if(refPropSchema.isString('uuid')) {
            let firstVoList: Node | undefined;
            let firstVoListMeta: ValueObjectMetadata | undefined;
            elementsTree.getFilteredElementsByType(NodeType.document).forEach(doc => {
              if (isListVo(doc)) {
                const listVoMeta = getVoMetadata(doc);
                const listVoSchema = listVoMeta.schema || new Schema({});

                if (listVoSchema.getListItemsSchema(new Schema({})).getRef() === refWithoutProp) {
                  firstVoList = doc;
                  firstVoListMeta = listVoMeta;
                }
              }
            })

            if (firstVoList) {
              columns.push({
                field: prop,
                ref: {
                  data: getSchemaRefName(firstVoList, firstVoListMeta!),
                  value: `data.${firstStringProp}`
                }
              })
            }
          }
        }
      }
    })

    if(!columns.length) {
      return uiSchema;
    }

    if(!uiSchema || !uiSchema['ui:table']) {
      return {'ui:table': {columns}, ...uiSchema};
    }

    if(uiSchema && uiSchema['ui:table']) {
      const uiColumns = (uiSchema['ui:table'] as {columns: TableColumn[]}).columns;

      columns.forEach(col => {
        const field = typeof col === 'string'? col : col.field;
        let fieldExists = false;

        uiColumns.forEach(uiCol => {
          const uiField = typeof uiCol === 'string' ? uiCol : uiCol.field;

          if(uiField === field) {
            fieldExists = true;
          }
        })

        if(!fieldExists) {
          uiColumns.push(col);
        }
      });

      (uiSchema['ui:table'] as {columns: TableColumn[]}).columns = uiColumns;
      return uiSchema;
    }
  }

  return uiSchema;
}

const suggestVoNamespace = (vo: Node): string => {
  const sourceVo = getConnectedValueObjectSourceIfPossible(vo);

  if(!sourceVo) {
    return ispConst.DEFAULT_DOC_NS;
  }

  const sourceMeta = getVoMetadata(sourceVo);

  return sourceMeta.ns || ispConst.DEFAULT_DOC_NS;
}

const getDefaultResolveConfigFromQuery = (query: Schema): ResolveConfig => {
  if(!query.isObject() || query.isEmpty()) {
    return {
      where: {
        rule: "always",
        then: {filter: {any: true}}
      }
    }
  }

  const firstProp = query.getObjectProperties()[0];

  if(query.isRequired(firstProp)) {
    return {
      where: {
        rule: "always",
        then: {filter: {eq: {prop: firstProp, value: `query.${firstProp}`}}}
      }
    }
  } else {
    return {
      where: {
        rule: "condition",
        if: `query.${firstProp}`,
        then: {filter: {eq: {prop: firstProp, value: `query.${firstProp}`}}},
        else: {filter: {any: true}}
      }
    }
  }
}

const updateDependenciesOnGraph = (vo: Node, ctx: ImmutableInformationContext, dependencies: List<Node>, graph: Graph): boolean => {
  const existingDependencies = List(Object.values(ctx.queryDependencies || {}).map(dep => dep.node));
  const existingDependencyIds = existingDependencies.map(dep => dep.getId());
  const changedDependencyIds = dependencies.map(dep => dep.getId());

  const dependenciesToDelete = existingDependencies.filter(dep => !changedDependencyIds.contains(dep.getId()));
  const dependenciesToAdd = dependencies.filter(dep => !existingDependencyIds.contains(dep.getId()));

  dependenciesToDelete.forEach(dep => graph.deleteNode(dep));

  const lastExistingDependency = existingDependencies.last(undefined);
  let anchorNode: Node | null = lastExistingDependency || vo;
  let position = NextToPosition.left;
  const margin: number | {x: number, y: number} = DEFAULT_CONNECT_MARGIN;

  dependenciesToAdd.forEach(dep => {
    const newDependency = makeNodeFromJs({
      id: graph.createCellId(),
      type: dep.getType(),
      name: dep.getName(),
      metadata: dep.getMetadata(),
    });

    if(!anchorNode) {
      anchorNode = vo;
    }

    if(newDependency.getType() === NodeType.document) {
      const connectedVOs = vo.getSources().filter(s => {
        if(s.getType() !== NodeType.document) {
          return false;
        }

        if(s.getName() === newDependency.getName()) {
          const sourceVoMeta = getVoMetadata(s);
          const newDepMeta = getVoMetadata(newDependency);

          return sourceVoMeta.ns === newDepMeta.ns;
        }

        return false;
      })

      if(connectedVOs.count()) {
        // Do not connect node twice, VOs have to be selected as dep by user, Cody does not suggest them to avoid conflicts with other connect scenarios like VO -> VO List
        return;
      }
    }

    graph.addNodeNextToAnother(newDependency, anchorNode as Node, position, margin);
    graph.connectNodes(newDependency, vo, undefined, undefined, true);
    markAsConnected(newDependency, graph);
    anchorNode = graph.getNode(newDependency.getId());
    position = NextToPosition.left;
  })

  return dependenciesToAdd.count() > 0 || dependenciesToDelete.count() > 0;
}
