import {Node, NodeType} from "../../../model/Graph";
import {WizardContext} from "./wizard-context";
import {Board} from "../../../model/Board";
import { RulesDependency } from "../rule-engine/types";
import { getVoMetadata } from "../vo/get-vo-metadata";
import { getSourcesOfType } from "../node/get-single-node-of-type";
import { getDependencyName } from "../rule-engine/get-dependency-name";
import { names } from "../node/names";
import { isList } from "../schema/shorthand";
import { isListVo } from "../vo/is-list-vo";
import { Schema } from "../schema/schema";
import { getSchemaRefName } from "../vo/get-schema-ref-name";

export interface WithInformationNode {
  vo: Node;
  sliceApiLabel: Node | null;
  queryDependencies: Readonly<Record<string, Readonly<RulesDependency>>>;
}

export type ImmutableInformationContext = Readonly<InformationContext>;

interface InformationContext extends WizardContext<ImmutableInformationContext>, WithInformationNode {

}

export const informationContextFromNode = (vo: Node, board: Board): ImmutableInformationContext => {
  const metadata = getVoMetadata(vo);

  const dependencyConfig = metadata.queryDependencies || {};

  const dependencies: Record<string, RulesDependency> = {};

  const tmpCtx: InformationContext = {
    board,
    type: "information",
    vo,
    sliceApiLabel: null,
    queryDependencies: {},
    nextStep: currentStep => null,
    previousStep: currentStep => null,
    installCodyGraphSuggestions: () => { /* Do nothing */ },
  }

  getSourcesOfType(vo, NodeType.document).forEach(voDep => {
    const queryFQCN = getDependencyName(voDep, tmpCtx);

    if(!metadata.queryDependencies || typeof metadata.queryDependencies[queryFQCN] === "undefined") {
      // Only add VOs as dependency if explicitly set by user
      return;
    }


    dependencies[queryFQCN] = {
      node: voDep,
      alias: dependencyConfig[queryFQCN] && dependencyConfig[queryFQCN].alias ? dependencyConfig[queryFQCN].alias : names(voDep.getName()).propertyName,
      options: dependencyConfig[queryFQCN] && dependencyConfig[queryFQCN].options ? dependencyConfig[queryFQCN].options : undefined,
    }
  });
  getSourcesOfType(vo, NodeType.externalSystem).forEach(serviceDep => {
    const serviceName = getDependencyName(serviceDep, tmpCtx);

    dependencies[serviceName] = {
      node: serviceDep,
      alias: dependencyConfig[serviceName] && dependencyConfig[serviceName].alias ? dependencyConfig[serviceName].alias : undefined,
      options: dependencyConfig[serviceName] && dependencyConfig[serviceName].options ? dependencyConfig[serviceName].options : undefined,
    }
  })

  const feature = getFeatureFromNode(vo);
  let sliceApiLabel: Node | null = null;

  if(feature && board.eventModelingEnabled) {
    feature.children().forEach(child => {
      if(child.getTags().contains(ispConst.TAG_SLICE_API_LABEL)) {
        sliceApiLabel = child;
      }
    })
  }

  const ctx: InformationContext = {
    board,
    type: "information",
    vo,
    sliceApiLabel,
    queryDependencies: dependencies,
    nextStep: currentStep => null,
    previousStep: currentStep => null,
    installCodyGraphSuggestions: () => { /* Do nothing */ },
  }

  return ctx;
}

export const isInformationContext = (ctx: WizardContext): ctx is ImmutableInformationContext => {
  return ctx.type === 'information';
}


const getFeatureFromNode = (node: Node): Node | null => {
  const parent = node.getParent();

  if(parent && parent.getType() === NodeType.feature) {
    return parent;
  }

  return null;
}
